$fonts: '~@renproject/fonts/fonts';

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Thin.otf') format('opentype');
    font-weight: 100;
    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Light.otf') format('opentype');
    font-weight: 200;
    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Book.otf') format('opentype');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Regular.otf') format('opentype');
