    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Bold.otf') format('opentype');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Black.otf') format('opentype');
