    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Mono.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

// Override PingFang with SuisseIntl
// Any project that is still using PingFang will use SuisseIntl instead

@font-face {
    font-family: 'PingFang SC';
