.network-fees {
    display: flex;
    width: 100%;

    > div > div {
        display: flex;
        font-size: 14px;
        align-items: center;
        justify-content: center;
        padding: 5px;
        flex-wrap: nowrap;

        > div {
            display: flex;
            flex-wrap: nowrap;
            margin-right: 5px;
        }

        img,
        svg {
            margin-right: 10px;
        }
    }
}

.network-fees-inner {
    @extend .no-scrollbar;
    overflow-x: scroll;

    display: flex;
    align-items: center;
}

.stat.network-fees-stat {
    overflow: hidden;
    min-width: 350px;

    .stat--title--outer {
        margin-bottom: 10px;
    }
}

.overview--bottom > div.volume--error {
    display: block;
    width: auto;
    margin: 0 auto;
    font-size: 14px;
}

.fee-row {
    margin: 3px 0;
    &--chain{
        display: flex;
        align-items: center;
    }
    &--icon-wrapper{
        display: flex;
        align-items: center;
        width: 30px
    }
    &--icon{
        margin-right: 0!important;
        width: 25px!important;
    }
}
